<template>
    <div class="content">
        <div class="search">
            <input @input="bindKeyInput" type="text" placeholder="搜索歌曲" class="inputSearch">
            <i @click="searchI" style="background:url(data:image/svg+xml;base64,); background-size:56rpx 56rpx; width:56rpx;height:56rpx;"></i>
        </div>
        <div style="margin-top:20rpx;">
            <div class="songitem">
                <div v-for="item in songitem" :key="item.id" class="songWidth" @click="songItem(item.id)">
                    <div class="songImg">
                        <img :src="item.coverImgUrl" alt="">
                        <p>
                            <i></i>{{item.playCount}}</p>
                    </div>
                    <p>{{item.name}}</p>
                </div>
            </div>
        </div>
        <div class="Playing" @click="listenerButtonPause">
            <div class='wrap'>
                <i style="background:url(data:image/svg+xml;base64,); background-size:80rpx 80rpx; width:80rpx;height:80rpx;margin-top:20rpx;margin-left:20rpx"></i>
            </div>

        </div>
    </div>
</template>

<script>
var Fly = require("flyio/dist/npm/wx"); //npm引入方式
var fly = new Fly(); //创建fly实例
export default {
    data() {
        return {
            songitem: {}
        };
    },
    onLoad: function() {
        wx.setNavigationBarTitle({
            title: "精品歌单推荐" //页面标题为路由参数
        });

        fly
            .get("http://localhost:3000/top/playlist/highquality?limit=30", {})
            .then(d => {
                this.songitem = d.data.playlists;
            })
            .catch(err => {
                console.log(err.status, err.message);
            });
    },

    mounted() {},
    methods: {
        bindKeyInput: function(e) {
            this.bindKeyInput = e.target.value;
        },
        searchI: function() {
            let searchValue = this.bindKeyInput;

            const url = "../search/main?search=" + searchValue;
            wx.navigateTo({ url });
        },

        songItem: function(e) {
            const url = "../songItem/main?id=" + e;
            wx.navigateTo({ url });
        },
        //监听button暂停按钮
        listenerButtonPause: function() {
            let e = wx.getStorageSync("musicId");

            if (!e) {
            } else {
                const url = "../music/main?id=" + e;
                wx.navigateTo({ url });
            }
        }
    }
};
</script>

<style scoped>
.content {
    display: flex;
    flex-direction: column;
    background: #fff;
    padding: 0 40rpx;
}
.search {
    display: flex;
}
.inputSearch {
    flex: 1;
    margin-right: 20rpx;
    border-radius: 60rpx;
    border: 1px solid #7054dd;
    font-size: 24rpx;
    padding-left: 20rpx;
}
.Playing {
    position: fixed;
    bottom: 20rpx;
    left: 311rpx;

    width: 120rpx;
    height: 120rpx;
    background: #fff;
    border: 2px solid #7054dd;
    border-radius: 50%;
    overflow: hidden;
}
.songitem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 30rpx;
}
.songWidth {
    width: 210rpx;
    margin-bottom: 30rpx;
}
.songWidth > p {
    padding-top: 10rpx;
    font-size: 24rpx;
    overflow: hidden;
    height: 68rpx;
}
.songImg {
    width: 210rpx;
    height: 210rpx;
    position: relative;
}
.songImg > p {
    width: 200rpx;
    font-size: 20rpx;
    background: rgba(0, 0, 0, 0.4);
    position: absolute;
    bottom: 0;
    right: 0;
    color: #fff;
    text-align: right;
    padding-right: 10rpx;

    border-radius: 0 0 10rpx 10rpx;
}
.songImg img {
    width: 210rpx;
    height: 210rpx;
    border-radius: 10rpx;
}
</style>



